* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.hero-page {
  background: linear-gradient(120deg, var(--blueGradient), var(--pinkGradient));
}

.people-affected {
  background: linear-gradient(45deg, var(--blueGradient), var(--pinkGradient));
}

.incluision-power {
  background: linear-gradient(120deg, var(--blueGradient), var(--pinkGradient));
}

.solution {
  background: linear-gradient(45deg, var(--blueGradient), var(--pinkGradient));
}

.solution-part2 {
  background: linear-gradient(120deg, var(--blueGradient), var(--pinkGradient));
}

.market {
  background: linear-gradient(45deg, var(--blueGradient), var(--pinkGradient));
}

.prizes {
  background: linear-gradient(120deg, var(--blueGradient), var(--pinkGradient));
}

.news {
  background: linear-gradient(45deg, var(--blueGradient), var(--pinkGradient));
}

.team {
  background: linear-gradient(120deg, var(--blueGradient), var(--pinkGradient));
}

.footer {
  height: 50dvh;
}

.hero-page img {
  height: 600px;
}

.hero-page .logo .subtitle p {
  background: linear-gradient(rgba(45, 44, 45, 0.15), rgba(202, 214, 252, 0.01));
  background-clip: text;
  -webkit-background-clip: text; /* Alguns navegadores precisam do prefixo */

  /* Terceiro passo: apagar o texto, deixando apenas o fundo atrás dele */
  -webkit-text-fill-color: transparent;
}

.hero-page .container-info {
  margin-top: -170px;
}

.incluision-power .general-data {
  background: linear-gradient(120deg, #F8A4DC, #F8B1AF);
  height: 300px;
}

.incluision-power .general-data .data .general-data-info {
  background: rgba(255, 255, 255, 0.25 );
  box-shadow: 2px 2px 16px 0 rgba(58, 58, 58, 0.05);
  backdrop-filter: blur(6.5px);
  -webkit-backdrop-filter: blur(6.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18 );
  padding: 30px;
}

.incluision-power .language-data img {
  height: 300px;
  width: auto;
  position: relative;
}

.incluision-power .language-data {
  margin-top: -75px;
}

.solution .economy img {
  height: 250px;
  width: 300px;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.market .title {
  height: 100px;
}

.market .cards-info {
  margin-top: -60px;
}

.market .cards-info .card {
  background: linear-gradient(120deg, #F8A4DC, #F8B1AF);
}

.prizes .prizes-info  {
  padding-top: 80px;
}

.prizes img {
  height: 200px;
  width: 300px;
  margin-top: -150px;
}

.prizes .title-recognize {
  padding-top: 80px;
}

.news .news-sp {
  padding-top: 80px;
}

.news .news-sp .news-info a {
  width: 160px;
  height: 48px;
}

.team .title {
  padding-top: 80px;
  margin-bottom: 160px;
}

.team .photos {
  display: flex;
  justify-content: center;
  align-items: center;
}

.team .photos .photo-henrique {
  margin-right: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
} 

.team .photos .photo-henrique .background {
  margin-left: 41px;
  margin-top: 4px;
}

.team .photos .photo-henrique span {
  margin-top: 30px;
}

.team .photos .photo-eduardo span {
  margin-top: 30px;
}

.team .photos .photo-eduardo .background {
  margin-left: 27px;
  margin-top: 4px;
}

.team .photos .photo-eduardo {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
} 

.team .photos .background {
  background: radial-gradient(#595757, #2F2F2F);
}

.team .photos img {
  margin-top: -60px;
}

@media screen and (max-width: 800px) {
  p {
    font-size: 18px;
  }

  .hero-page {
    display: flex;
    flex-direction: column;
  }

  .hero-page .container-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
  }

  .hero-page .container-info .hero-page-title {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    margin-bottom: 50px;
  }

  .hero-page .container-info .hero-page-title h2 {
    font-size: 30px;
  }

  .hero-page .container-info img {
    height: 300px;
    width: 250px;
  }

  .hero-page .container-info .hero-page-subtitle {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 30px;
    padding: 0;
  }

  .hero-page .container-info .hero-page-subtitle p {
    text-align: center;
    font-size: 18px;
  }

  .hero-page .logo .subtitle p {
    font-size: 50px;
  }

  .people-affected {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 80px;
  }

  .people-affected .graph .graph-count img {
    width: 300px;
  }

  .people-affected .graph .graph-subtitle {
    margin-top: -20px;
    padding-left: 20px;
    padding-right: 20px;
  }

  .people-affected .numbers-info {
    display: flex;
    flex-direction: column;
    padding-top: 50px;
    flex: wrap;
    max-width: 350px;
  }

  .people-affected .numbers-info .totalCountSeveralDisease {
    padding-top: 50px;
  }

  .incluision-power {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .incluision-power .general-data {
    height: 600px;
    width: 100dvw;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 80px;
  }

  .incluision-power .general-data .data {
    display: flex;
    flex-direction: column;
    padding: 20px;
  }

  .incluision-power .language-data {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-top: 130px;
  }

  .incluision-power .language-data img {
    width: 120px;
    height: 200px;
  }

  .incluision-power .language-data .language-data-about {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    padding-top: 80px;
  }

  .solution {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .solution .economy {
    display: flex;
    flex-direction: column;
  }

  .solution .economy img {
    width: 200px;
    height: 200px;
    margin-bottom: 40px;
  }

  .solution .economy .economy-data {
    text-align: center;
    padding-bottom: 80px;
    padding-top: 80px;
  }

  .solution .economy .hand-coin {
    height: 150px;
  }

  .solution-part2 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 40px;
  }

  .solution-part2 iframe {
    width: 350px;
    height: 300px;
  }

  .solution-part2 .benefits {
    display: flex;
    flex-direction: column;
  }

  .market {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0;
    padding-top: 40px;
  }

  .market .cards-info {
    display: flex;
    flex-direction: column;
    padding-top: 80px;
  }

  .market .cards-info .card {
    width: 300px;
    height: 350px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
  }

  .prizes {
    display: flex;
    flex-direction: column;
  }

  .prizes .prizes-info {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 150px;
  }

  .prizes .prizes-info .threem-prize {
    padding-bottom: 200px;
  }

  .prizes .prizes-info img {
    width: 200px;
    height: 150px;
  }

  .prizes .title-recognize {
    padding-top: 80px;
  }

  .prizes .title-recognize h1 {
    font-size: 40px;
  }

  .news {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .news .news-sp {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 80px;
    max-width: 400px;
    width: 400px;
  }

  .news .news-sp .news-info {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 80px;
    padding-left: 20px;
    padding-right: 20px;
  }

  .news .news-sp img {
    width: 350px;
    height: 350px;
  }

  .news .news-cps {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 80px;
    max-width: 400px;
    width: 400px;
  }

  .news .news-cps img {
    width: 350px;
    height: 350px;
  }  

  .news .news-cps .news-info {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top  : 80px;
    padding-left: 20px;
    padding-right: 20px;
  }

  .team {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0;
    padding-top: 80px;
  }

  .team .photos {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .team .photos .photo-henrique {
    margin-right: 0px;
    margin-bottom: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .team .photos .photo-henrique img {
    margin-left: 20px;
  }

  .team .photos .photo-henrique span {
    padding-top: 40px;
  }

  .team .photos .photo-eduardo {
    margin-bottom: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
  }

  .team .photos .photo-eduardo img {
    margin-left: 0px;
  }

  .team .photos .photo-eduardo span {
    padding-top: 40px;
  }

  .team .photos .photo-henrique .background {
    width: 210px;
    height: 200px;
  }

  .team .photos .photo-eduardo .background {
    width: 210px;
    height: 200px;
  }

  .team .infos {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .footer img {
    width: 300px;
  }
}